import React, { Component } from 'react';
import './Reception.css';
import NumberFormat from 'react-number-format';
import moment from 'moment';
import Newheader from './Newheader';
import Adlogin from './Adlogin';
import Spinner from 'react-bootstrap/Spinner';



const getHotelAddress = "https://orangegroove.localto.net/hoteladdress";
const getWorkDate = "https://orangegroove.localto.net/getActive";
const getGuestBalances = "https://orangegroove.localto.net/owingGuests";



class GuestBalances extends Component {
    constructor(props) {
        super(props);
        console.log(">>>Inside CashConstructor",props)
        this.state = {
            
            date:'',
            loginDetails:'',
            name:localStorage.getItem('userInfo'),
            login:'',
            Blogin:'',
            shift:'',
            

            transactionDate:'',
            guestBal: '',

            Hotelname:'',
            Hoteladdress:'',
            Hotelphone:''
            
            
        };
        

    }
    
    logout(){
        localStorage.removeItem('userInfo');
        this.setState({
            login:true
        })
        
    }

    
    

    renderRoomSales=(data)=>{
        if(data){
            return data.map((item)=>{
                data.sort((a, b) => a.date - b.date);
                return(
                    <>
                        <tr key= {item._id}>
                            
                            <td className="table-light table-striped ">{item.roomNumbers}</td>
                            <td className="table-light table-striped  ">{item.fname} {item.lname}</td>
                            <td className="table-light table-striped  "><NumberFormat value={item.totalDeposits}thousandSeparator={true}displayType={"text"}/></td>
                            <td className="table-light table-striped  "><NumberFormat value={item.totalBills}thousandSeparator={true}displayType={"text"}/></td>
                            <td className="table-light table-striped  "><NumberFormat value={item.totalRoomRates}thousandSeparator={true}displayType={"text"}/></td>
                            <td className="table-light table-striped  "><NumberFormat value={item.balance}thousandSeparator={true}displayType={"text"}/></td>
                            
                            
                        </tr>
                        
                    </>
                )
               
            })
            
        }
        else {
            return(
                <>
                    <Spinner animation="border" variant="primary" />
                    <Spinner animation="border" variant="danger" />
                    <Spinner animation="border" variant="warning" />
                </>
            )
        }
           
    }

    
    render() {
        console.log (">>> Inside Cashdetails", this.state)
        let defaultDate = moment(this.state.transactionDate).format('MMM DD YYYY');
        if(localStorage.getItem('rtk')==null||localStorage.getItem('userdata')===null||localStorage.getItem('emailAD')==='admin@orangegroove.com'){
          return(
              <>
                  <Adlogin/>
              </>
          )

      }
        return (
          <>
          <Newheader/>
            <div className ="container">
              
                
                <div>
                    <center>
                        <h6>{this.state.Hotelname}</h6>
                        <p className="textSize">{this.state.Hoteladdress}</p>
                        <p className="textSize">{this.state.Hotelphone}</p>
                       
                    </center>
                </div>
                <center>
                    <h5>Daily Guest Balances Report</h5><br/>
                    <p>Date: {defaultDate}</p>
                    
                </center>
                
                <table className="table table-hover">
                    
                    <thead className="table-warning">
                        <tr>
                            
                            <th className="adjust50">Room Number</th>
                            <th className="adjust50">Names</th>
                            <th className="adjust50">Total Deposit</th>
                            <th className="adjust50">Total Room Service</th>
                            <th className="adjust50">Total Accommodation</th>
                            <th className="adjust50">Balance</th>
                            
                            
                                    
                        </tr>
                    </thead>
                    <tbody className="table table-hover">
                        {this.renderRoomSales(this.state.guestBal)}
                                              
                        
                        
                    </tbody>
                </table>
                
                
                <hr/>
                <center>
                
                    <button className="btn btn-danger movebtn printing" onClick={ () => this.props.history.push('/') }>Close</button>
                    <button className="btn btn-primary movebtn printing" onClick={ () => window.print() }>Print</button>
                    
                        
                </center>       
            </div>
          </>
            
        );
    }
  
    componentDidMount() {
        console.log(">>> Inside CashDidMount", this.state)
        fetch(`${getGuestBalances}`, {method:'GET'})
        .then((res) => res.json())
        .then((data) => {
            this.setState({guestBal:data});
            
        })

        fetch(`${getWorkDate}`, {method:'GET'})
        .then((res) => res.json())
        .then((data) => {
            data.map((item)=>{
                this.setState({transactionDate:item.date})
                return 'ok'
            })
            
        
        })



        fetch(`${getHotelAddress}`, {method:'GET'})
        .then((res) => res.json())
        .then((data) => {
            data.map((item)=>{
                this.setState({
                    Hotelname:item.HotelName,
                    Hoteladdress:item.Address,
                    Hotelphone:item.Phone
                
                })
                return 'ok'
            })
            
        })
            
    
        

    }

       
       
       
    


}


export default GuestBalances;